.features {
  background-image: url("../img/triangls-about.png");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: 1920px 1347px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/triangls-about2x.png");
  }

  @media (max-width: $m-end) {
    background-image: url("../img/triangls-about-t.png");
    background-size: 559px 873px;
    background-position: 100% 6%;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/triangls-about-t2x.png");
    }
  }

  @media (max-width: $s-end) {
    background-position: center 72%;
    background-size: 320px 873px;
  }
}

.features__section {
  padding-top: 112px;

  @media (max-width: $m-end) {
    padding-top: 53px;
  }

  @media (max-width: $s-end) {
    padding-top: 37px;
  }
}

.features__list {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  justify-content: space-between;

  @media (max-width: $m-end) {
    padding: 0;
  }
}

.features__item span {
  font-family: $roboto;
  font-weight: normal;
  font-size: 24px;
  line-height: 34px;
  color: $blue;
  display: block;
  margin-bottom: 16px;
  min-width: 0;
  word-break: break-word;

  @media (max-width: $m-end) {
    margin-bottom: 8px;
  }

  @media (max-width: $s-end) {
    font-size: 20px;
    line-height: 140.62%;
  }
}

.features__item {
  list-style: none;
  position: relative;
  flex: 0 1 48.8%;
  padding: 97px 38px 65px 43px;
  margin-bottom: 29px;
  cursor: pointer;
  background-color: $white;
  border: 1px solid $light-gray;

  &::before {
    @include pseudo();
    @include size(3px, 100%);
    background-color: $blue;
    left: 0;
    top: 0;
  }

  &--confidentiality,
  &--price {
    padding-bottom: 86px;
  }

  &:hover {
    background-color: $blue;
    transition: all 700ms ease-in;

    svg {
      fill: $white;
    }

    span,
    p {
      color: $white;
    }
  }

  @media (max-width: $m-end) {
    padding: 65px 18px 22px 29px;
    margin-bottom: 29px;
    flex: 0 0 345px;

    &--confidentiality,
    &--price {
      padding-bottom: 36px;
    }

    &::before {
      width: 2px;
    }
  }

  @media (max-width: $s-end) {
    flex-basis: 100%;
    padding: 72px 18px 25px 26px;
    margin-bottom: 18px;

    &--suppliers,
    &--confidentiality {
      padding-right: 11px;
    }

    &--confidentiality {
      padding-bottom: 57px;
      padding-top: 63px;

      span {
        margin-bottom: 18px;
      }
    }

    &--price {
      padding-bottom: 18px;

      span {
        margin-bottom: 16px;
      }
    }
  }
}

.features__item svg {
  fill: $blue;
  position: absolute;
  top: 41px;

  @media (max-width: $m-end) {
    top: 24px;
  }
}


.features__item p {
  font-family: $mono;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $gray;
  margin-top: 0;
  margin-bottom: 0;
  min-width: 0;
  word-break: break-word;
}
